"use client"

import { useState } from "react"
import { AdminLayout } from "@/components/admin-layout"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { mockUsers } from "@/lib/mock-data"
import { Gift, CheckCircle } from "lucide-react"

export default function GiveTokensPage() {
  const [selectedUserId, setSelectedUserId] = useState("")
  const [tokenAmount, setTokenAmount] = useState("")
  const [success, setSuccess] = useState(false)

  const selectedUser = mockUsers.find((u) => u.id === selectedUserId)

  const handleGiveTokens = () => {
    if (!selectedUserId || !tokenAmount) return
    setSuccess(true)
    setTimeout(() => {
      setSuccess(false)
      setSelectedUserId("")
      setTokenAmount("")
    }, 3000)
  }

  return (
    <AdminLayout>
      <div className="space-y-6">
        <div>
          <h1 className="text-2xl font-bold text-foreground">Give Tokens</h1>
          <p className="text-muted-foreground">Award tokens to users</p>
        </div>

        <Card className="max-w-xl border-border bg-card">
          <CardHeader>
            <CardTitle className="text-card-foreground">Token Distribution</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="user" className="text-card-foreground">
                Select User
              </Label>
              <Select value={selectedUserId} onValueChange={setSelectedUserId}>
                <SelectTrigger className="border-border bg-input text-foreground">
                  <SelectValue placeholder="Choose a user" />
                </SelectTrigger>
                <SelectContent className="border-border bg-popover">
                  {mockUsers.map((user) => (
                    <SelectItem key={user.id} value={user.id}>
                      <div className="flex flex-col">
                        <span>{user.name}</span>
                        <span className="text-xs text-muted-foreground">{user.email}</span>
                      </div>
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {selectedUser && (
              <div className="rounded-lg bg-secondary/50 p-3">
                <p className="text-sm text-muted-foreground">Current Balance</p>
                <p className="text-xl font-bold text-primary">{selectedUser.tokens} tokens</p>
              </div>
            )}

            <div className="space-y-2">
              <Label htmlFor="tokens" className="text-card-foreground">
                Number of Tokens
              </Label>
              <Input
                id="tokens"
                type="number"
                placeholder="Enter token amount"
                value={tokenAmount}
                onChange={(e) => setTokenAmount(e.target.value)}
                className="border-border bg-input text-foreground placeholder:text-muted-foreground"
              />
            </div>

            <Button
              onClick={handleGiveTokens}
              disabled={!selectedUserId || !tokenAmount || success}
              className="w-full bg-primary text-primary-foreground hover:bg-primary/90"
            >
              {success ? (
                <>
                  <CheckCircle className="mr-2 h-4 w-4" />
                  Tokens Sent!
                </>
              ) : (
                <>
                  <Gift className="mr-2 h-4 w-4" />
                  Give Tokens
                </>
              )}
            </Button>
          </CardContent>
        </Card>
      </div>
    </AdminLayout>
  )
}
